main .icon-view {
  width: 532px;
  height: 558px;
  margin-left: 40px;
  float: left;
  position: relative;
}

main .icon-view #size {
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  color: rgba(255, 255, 255, 0.35);
  text-align: center;
  font-family: 'Menlo', monospace;
  font-size: 11px;
  font-weight: 200;
  background-color: transparent;
}

main .icon-view #size.alert {
  color: #E14540;
}

