main .icon-view #icon {
  width: 512px;
  height: 512px;
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 0;
}

main .icon-view #dropzone {
  position: absolute;
  top: 0;
  left: 0;
  width: 532px;
  height: 532px;
  border-radius: 10px;
  background-image: none;
  background-repeat: no-repeat;
  background-position: center center;
  opacity: 0;
  z-index: 1;
  transition: opacity .15s ease-in-out;
}

