main .form {
  padding: 0 30px 0 20px;
  width: 248px;
  height: 532px;
  float: right;
  font-weight: 600;
  position: relative;
}

main .form label {
  display: block;
  font-size: 13px;
  padding-bottom: 10px;
}

main .form label:not(:first-of-type) {
  padding-top: 10px;
  border-top: 1px solid rgba(255, 255, 255, 0.15);
}

main .form label input {
  border-radius: 50%;
}

main .form table {
  display: none;
  width: 80%;
  margin: 5px 0  5px 18px;
  font-family: 'Menlo', monospace;
  font-size: 11px;
  line-height: 11px;
  color: #01F952;
  position: relative;
}

main .form table td {
  padding: 2px 4px;
}

main .form table td:last-child {
  color: #7F37C5;
}

main .form .preview {
  width: 128px;
  height: 128px;
  position: absolute;
  bottom: 60px;
  left: 54px;
}

main .form .preview.active {
  outline: 1px dashed rgba(52, 192, 249, 0.5);
}

main .form #sizes {
  width: 256px;
}

main .form datalist {
  display: flex;
  justify-content: space-between;
}

main .form datalist option {
  font-size: 10px;
}

main .form #generate {
  display: block;
  width: 196px;
  height: 30px;
  position: absolute;
  bottom: 0;
  background: #031320;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 4px;
  font-size: 14px;
  color: #fff;
  font-weight: 500;
  text-align: center;
  transition: all .15s ease-in-out;
}

main .form #generate[disabled] {
  color: rgba(255, 255, 255, 0.3);
}

main .form #generate:hover:not([disabled]) {
  background-color: rgba(255, 255, 255, 0.1);
}

main .form #generate:active:not([disabled]) {
  background-color: #020b12;
  box-shadow: inset 0 2px 4px #000204;
  outline: none;
}

:focus {
  outline: none;
  box-shadow: 0 0 1px 2px rgba(52, 192, 249, 0.75);
}
